*{
    margin: 0;
    padding: 0;
}
.background{
    background-color: #CCCCFF;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.textareacontroller{
    background-color: white;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 1rem;
    font-size: 1.5rem;
    border: 1px solid gray;
    margin-top: 1rem;
    width: 90vw;
    min-height: 70vh;
}
.textareacontroller p{
    color: black;
}
.micbtn{
    background-color: #FF7F50;
    width: 10rem;
    height: 10rem;
    margin-top: 1rem;
    border-radius: 10rem;
    border: 3px solid white;
    cursor: pointer;
}
.micbtn:active{
    background-color: #DE3163;
    
}

.trash{
    width: 5rem;
    height: 5rem;
    bottom: 10vh;
    position: absolute;
    right: 2vw;
    bottom: 50%;
    transform: translateY(50%);
    border-radius: 10rem;
    background-color: black;
    cursor: pointer;
    border: 3px solid white;
}
.trash:active{
    background-color: gray;
}
.btnCon{
    width: 90vw;
    
    position: relative;
    display: flex;
    justify-content: center;
}


@media only screen and (max-width:480px){
    .mic{
        width: 2rem;
        height: 2rem;
    }
    .dustbin{
        width: 1.5rem;
        height: 1.5rem;
    }
    .micbtn{
    width: 4.5rem;
    height: 4.5rem;
   }
    .trash{
        width: 3.5rem;
        height: 3.5rem;
        right: 1vh;
        
    }
    .textareacontroller{
        font-size: 1rem;
    }
}

